.advantages {
  padding: 121px var(--side-offset) 60px;

  @media (max-width: $vp-1023) {
    padding-top: 80px;
    padding-bottom: 40px;
  }

  @media (max-width: $vp-767) {
    padding-top: 50px;
    padding-bottom: 25px;
  }
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style-type: none;

  @media (max-width: $vp-767) {
    display: block;
  }
}

.advantages__item {
  position: relative;
  min-height: 300px;
  padding: 40px 45px;
  outline: 1px solid #ebebeb;

  @media (max-width: $vp-1023) {
    min-height: 360px;
    padding: 40px 44px 32px 45px;
  }

  @media (max-width: $vp-767) {
    min-height: 340px;
    padding: 30px;
  }

  &--small {
    @media (max-width: $vp-767) {
      min-height: 325px;
    }
  }

  &:not(:last-child) {
    @media (max-width: $vp-767) {
      margin-bottom: 30px;
    }
  }

  &::before {
    position: absolute;
    left: 0;
    top: 0;
    width: 5px;
    height: 100%;
    content: "";
    background-color: $color-deep-sky-blue;
  }

  &:hover {
    background-color: $color-deep-sky-blue;

    .advantages__title,
    .advantages__description,
    .advantages__icon {
      color: $color-alice-blue;
      fill: $color-alice-blue;
    }
  }
}

.advantages__icon {
  margin-bottom: 13px;
  fill: $color-deep-sky-blue;

  @media (max-width: $vp-1023) {
    margin-bottom: 9px;
  }

  @media (max-width: $vp-767) {
    width: 30px;
    height: 30px;
  }
}

.advantages__title {
  margin-bottom: 15px;

  @media (max-width: $vp-1023) {
    margin-bottom: 9px;
  }
}

.advantages__description {
  margin: 0;
}
